import { createApp } from 'vue'
import { createPinia } from 'pinia'
import { createPersistedState } from 'pinia-plugin-persistedstate'
import * as Sentry from '@sentry/vue'

import App from './App.vue'
import { router } from './router'

import '@/styles/index.scss'
import '@unocss/reset/tailwind.css'
import 'uno.css'

const app = createApp(App)
// 配置 pinia 持久化
const pinia = createPinia()
pinia.use(
    createPersistedState({
        key: id => `__${id}`,
    }),
)
app.use(router)
app.use(pinia)

Sentry.init({
    app,
    dsn: 'https://b5b05165c2854aa52893ae652d7c70a6@o4507501672333312.ingest.us.sentry.io/4507501677117440',
    integrations: [
        Sentry.browserTracingIntegration(),
        Sentry.replayIntegration(),
    ],
    // 性能监控
    tracesSampleRate: 1.0, // 捕获 100% 的交易
    // 设置“tracePropagationTargets”来控制应为哪些 URL 启用分布式跟踪
    tracePropagationTargets: ['localhost', /^https:\/\/yourserver\.io\/api/],
    // 会话重播
    replaysSessionSampleRate: 0.1, // 这会将采样率设置为 10%。您可能希望在开发过程中将其更改为 100%，然后在生产过程中以较低的采样率进行采样。
    replaysOnErrorSampleRate: 1.0, // 如果您尚未对整个会话进行采样，请在对出现错误的会话进行采样时将采样率更改为 100%。
})

app.mount('#app')
